@use "./_variant" as setting;

body {
  width: 100%;
  line-height: 1.5;
  font-family: 'M PLUS Rounded 1c', 'Noto Sans JP', sans-serif;
  color: var(--color_text);
  background-color: var(--color_base);
}

img {
  display: inline-block;
  height: auto;
}

.w_inner {
  max-width: 1288px;
  margin-left: auto;
  margin-right: auto;
}

section>.inner {
  padding: 36px 8px;
}

.ol {
  outline: 1px solid var(--color_border_darker);
}