@use "./_variant" as setting;


//**********************************************************
// rf(riverfield)
//**********************************************************
// ・Grid                            済
// ・TopBar                          済
// ・BottomBar                       済
// ・Modal                           済
// ・Dialog                          済
// ・BottomSheet                     済
// ・SideSheet                       済
// ・NavDrawer                       済
// ・Tab                             済
// ・Scrim                           済
// ・閉じるボタン                     済
// ・font-size（タイトル , テキスト）  済


//-----------------------------
// common
//-----------------------------
.Modal_headline,
.Dialog_headline,
.BottomSheet_headline,
.NavDrawer_headline {
  display: flex;
  align-items: center;
  text-align: center;
  min-height: 40px;
  padding: 0 4px;
  color: var(--color_text_primary);

  .Modal_title,
  .BottomSheet_title,
  .Dialog_title,
  .NavDrawer_title {
    margin: 0 auto;
  }
}



//-----------------------------
// Grid
//-----------------------------
.Grid {
  .row {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;

    >* {
      flex-grow: 0;
      flex-shrink: 0;
    }

    @for $i from 1 through 12 {
      [class*="col-#{$i}"] {
        width:calc(100% / 12 * #{$i});
        padding: 0.75rem;
      }
    }

    @media screen and (min-width: 767px) {
      @for $i from 1 through 12 {
        [class*="-#{$i}-"] {
          width:calc(100% / 12 * #{$i});
          padding: 0.75rem;

        }
      }
    }

    @media screen and (min-width: 1023px) {
      @for $i from 1 through 12 {
        [class$="-#{$i}"] {
          width:calc(100% / 12 * #{$i});
          padding: 0.75rem;

        }
      }
    }
  }
}



//-----------------------------
// TopBar
//-----------------------------
.TopBar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  border-bottom: 1px solid var(--color_border_lighter);
  background-color: var(--color_base);

  .TopBar_container {
    min-height: 56px;
    display: grid;
    grid-template-columns: 10% 1fr minmax(10%, auto);
    align-items: center;
    text-align: center;
    padding: 12px;
  }

  .TopBar_navIcon {
    margin-right: auto;
  }

  .TopBar_navIcon>* {
    display: block;
  }

  .TopBar_title {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
  }

  .TopBar_interactive {
    margin-left: auto;
  }
}



//-----------------------------
// BottomBar
//-----------------------------
.BottomBar {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  font-size: 12px;
  border-top: 1px solid var(--color_border_lighter);
  background-color: var(--color_base);

  .BottomBar_link {
    display: block;
    padding: 8px 0;
  }

  .BottomBar_shadow,
  .BottomBar_label {
    display: block;
    text-align: center;
    margin: 0 auto;
  }

  .BottomBar_shadow {
    width: 52px;
    min-width: 52px;
    padding: 0 2px;
    margin-bottom: 4px;
    border-radius: 14px;
  }

  .BottomBar_link.current {
    .BottomBar_shadow {
      background-color: var(--color_gray_9);
    }

    .BottomBar_label {
      font-weight: bold;
    }
  }
}

.BottomBar:has(.BottomBar_link:first-child:nth-last-child(4),
  .BottomBar_link:first-child:nth-last-child(4)~.BottomBar_link) {
  grid-template-columns: repeat(4, 1fr);
}

.BottomBar:has(.BottomBar_link:first-child:nth-last-child(5),
  .BottomBar_link:first-child:nth-last-child(5)~.BottomBar_link) {
  grid-template-columns: repeat(5, 1fr);
}




//-----------------------------
// Modal
//-----------------------------
.Modal {
  opacity: 1;
  visibility: visible;
  position: fixed;
  inset: 50% auto auto 50%;
  translate: -50% -50%;
  width: 80vw;
  max-width: 480px;
  min-height: 160px;
  padding: 8px;
  border-radius: 8px;
  background-color: var(--color_base);
  z-index: var(--z_index_cover);
}

.Modal.hidden {
  opacity: 0;
  visibility: hidden;
}



//-----------------------------
// Dialog
//-----------------------------
.Dialog {
  opacity: 1;
  visibility: visible;
  position: fixed;
  inset: 50% auto auto 50%;
  translate: -50% -50%;
  width: 80vw;
  max-width: 480px;
  min-height: 160px;
  padding: 8px;
  border-radius: 8px;
  background-color: var(--color_base);
  z-index: var(--z_index_cover);
  
  .Dialog_container {
    padding: 16px 8px;
    min-height: 80px;
    border-top: 1px solid var(--color_border_light);
    border-bottom: 1px solid var(--color_border_light);
  }

  .Dialog_interactive {
    display: flex;
    justify-content: space-around;
    text-align: center;
    padding: 16px;

    .Dialog_cancel,
    .Dialog_ok {
      min-width: 80px;
    }

    .Dialog_ok {
      background-color: var(--color_gray_4);
      color: var(--color_text_light);
    }
  }
}

.Dialog.hidden {
  opacity: 0;
  visibility: hidden;
}



//-----------------------------
// BottomSheet
//-----------------------------
.BottomSheet {
  position: fixed;
  bottom: 0;
  left: 0;
  transform: translateY(0);
  width: 100vw;
  height: auto;
  min-height: 320px;
  padding: 8px;
  transition: 0.2s;
  border-top-left-radius: 14px;
  border-top-right-radius: 14px;
  background-color: var(--color_base);
  z-index: var(--z_index_cover);
}

.BottomSheet.hidden {
  position: fixed;
  bottom: 0;
  left: 0;
  transform: translateY(100%);
}



//-----------------------------
// SideSheet
//-----------------------------
.SideSheet {
  position: fixed;
  top: 0;
  right: 0;
  width: 60vw;
  max-width: 480px;
  max-height: 90vh;
  padding: 8px;
  border-radius: 16px;
  transform: translate(0);
  transition: 0.2s;
  background-color: var(--color_base);
  z-index: var(--z_index_cover);
}

.SideSheet.hidden {
  transform: translate(100%,-100%);
}



//-----------------------------
// navDrawer
//-----------------------------
.NavDrawer {
  position: fixed;
  top: 0;
  left: 0;
  width: 70vw;
  max-width: 480px;
  height: 100vh;
  min-height: 320px;
  padding: 8px;
  transform: translateX(0);
  transition: 0.2s;
  background-color: var(--color_base);
  z-index: var(--z_index_cover);
}

.NavDrawer.hidden {
  transform: translateX(-100%);
}



//-----------------------------
// Tab
//-----------------------------
.Tab {
  .Tab_headline {
    display: flex;
    gap: 16px;
    justify-content: space-around;
  }

  .Tab_title {
    cursor: pointer;
    width: 100%;
    text-align: center;
  }

  .Tab_title.active {
    border-bottom: 3px solid var(--color_gray_7);
  }

  .Tab_container {
    position: relative;
  }

  .Tab_panel {
    position: absolute;
    opacity: 0;
    visibility: hidden;
    width: 100%;
    transform: translateX(-100%);
    transition: 0.2s;
  }

  .Tab_panel.active {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
    transition: 0.2s;
  }
}



//-----------------------------
// Scrim
//-----------------------------
.Scrim {
  opacity: 1;
  visibility: visible;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(var(--color_black_rgb), 0.5);
  transition: 0.2s;
  cursor: pointer;
  z-index: calc(var(--z_index_cover)-1);
}

.Scrim.hidden {
  opacity: 0;
  visibility: hidden;
  transition: 0.2s;
}



//-----------------------------
// ×アイコン
//-----------------------------
[class^="Dialog"][class*="_close"],
[class^="BottomSheet"][class*="_close"],
[class^="SideSheet"][class*="_close"] {
  position: relative;
  width: 32px;
  height: 32px;
  border-radius: 4px;
}

[class^="Dialog"][class*="_close"]::before,
[class^="Dialog"][class*="_close"]::after,
[class^="BottomSheet"][class*="_close"]::before,
[class^="BottomSheet"][class*="_close"]::after,
[class^="SideSheet"][class*="_close"]::before,
[class^="SideSheet"][class*="_close"]::after {
  position: absolute;
  content: "";
  inset: 0;
  margin: auto;
  width: 1.5px;
  height: 90%;
  background-color: var(--color_gray_4);
  border-radius: 3px;
}

[class^="Dialog"][class*="_close"]::before,
[class^="BottomSheet"][class*="_close"]::before,
[class^="SideSheet"][class*="_close"]::before {
  rotate: 45deg;
}

[class^="Dialog"][class*="_close"]::after,
[class^="BottomSheet"][class*="_close"]::after,
[class^="SideSheet"][class*="_close"]::after {
  rotate: -45deg;
}



//-----------------------------
// font-size
//-----------------------------
// タイトル
.fs-h1 {
  font-size: 28px;

  ::placeholder {
    font-size: 28px;
  }
}

.fs-h2 {
  font-size: 26px;

  ::placeholder {
    font-size: 26px;
  }
}

.fs-h3 {
  font-size: 24px;

  ::placeholder {
    font-size: 24px;
  }
}

// テキスト
.fs-t1 {
  font-size: 18px;

  ::placeholder {
    font-size: 18px;
  }
}

.fs-t2 {
  font-size: 16px;

  ::placeholder {
    font-size: 16px;
  }
}

.fs-t3 {
  font-size: 14px;

  ::placeholder {
    font-size: 14px;
  }
}